@import "./../../sassStyles/index";

.icon {

    width: 100%;
    height: 100%;

    & svg {
        width: 100%;
        height: 100%;
    }

    &__text {
        fill: $font-color;
        stroke: $font-color;
        stroke-width: 7;
    }

    &__bar {
        transition: all 0.5s ease-in-out;
        fill: #FFF;
        stroke: #FFF;
        stroke-width: 7;
    }
}